<template>
  <div class="container" >
    <br>
      <div class="row">

        <div class="col-lg-6" v-if="bmwItem!=0">
          <br>
          <div class="border">
              <h2>BMW (price: {{bmwPrice}}) ( Quantity : {{ bmwItem }})</h2>
              <input type="text" class="form-control" placeholder="Quantity" v-model="curbmw">
            <button type="button" class="btn btn-danger" @click="sellBmwitem">Sell</button>
          </div>
        </div>

        <div class="col-lg-6" v-if="googleItem!=0">
            <br>
          <div class="border">
              <h2>Google (price: {{googlePrice}}) ( Quantity : {{ googleItem }})</h2>
              <input type="text" class="form-control" placeholder="Quantity" v-model="curgoogle">
            <button type="button" class="btn btn-danger" @click="sellGoogleitem">Sell</button>
          </div>
        </div>


      </div>
      <br>
      <div class="row">
        <div class="col-lg-6" v-if="appleItem!=0">
          <br>
          <div class="border">
              <h2>Apple (price: {{applePrice}}) ( Quantity : {{ appleItem }})</h2>
              <input type="text" class="form-control" placeholder="Quantity" v-model="curapp">
            <button type="button" class="btn btn-danger" @click="sellAppleitem">Sell</button>
          </div>
        </div>

        <div class="col-lg-6" v-if="twitterItem!=0">
                    <br>
          <div class="border">
              <h2>Twitter (price: {{twitterPrice}}) ( Quantity : {{ twitterItem }})</h2>
              <input type="text" class="form-control" placeholder="Quantity" v-model="curtwitt">
            <button type="button" class="btn btn-danger"  @click="sellTwitteritem">Sell</button>
          </div>
        </div>


      </div>


      </div>
  </div>
</template>

<script>
import {mapGetters } from 'vuex';

export default {

  data() {
    return {
      curbmw :'',
     curapp:'',
     curgoogle:'',
     curtwitt:''
    }
  },
  computed: {
    ...mapGetters ([
      'bmwItem',
      'appleItem',
      'googleItem',
      'twitterItem',
      'bmwPrice',
      'applePrice',
      'googlePrice',
      'twitterPrice',
      'getfund'
    ])

  },
  methods: {
    sellBmwitem() {
      if(this.bmwItem-this.curbmw>=0){
      this.$store.commit('sellBmwitem',this.curbmw);
      var price=this.curbmw*this.bmwPrice;
      this.$store.commit('sellFunds',price);
      this.curbmw=null;
    }else {
      alert('Not Enough Items');
    }
    },
    sellAppleitem() {
        if(this.appleItem-this.curbmw>=0){
      this.$store.commit('sellAppleitem',this.curapp);
      var price=this.curapp*this.applePrice;
      this.$store.commit('sellFunds',price);
      this.curapp=null;
    }else {
      alert('Not Enough Items');
    }
    },
    sellGoogleitem() {
        if(this.googleItem-this.curbmw>=0){
      this.$store.commit('sellGoogleitem',this.curgoogle);
      var price=this.curgoogle*this.googlePrice;
      this.$store.commit('sellFunds',price);
      this.curgoogle=null;
    }else {
      alert('Not Enough Items');
    }
    },
    sellTwitteritem() {
        if(this.twitterItem-this.curbmw>=0){
      this.$store.commit('sellTwitteritem',this.curtwitt);
      var price=this.curtwitt*this.twitterPrice;
      this.$store.commit('sellFunds',price);
      this.curtwitt=null;
    }else {
      alert('Not Enough Items');
    }
    },
  }

}
</script>
<style scoped>

h2{
  background-color: #fcdada;
  color: darkgreen;
  border-radius: 3px;
  font-size: 15px;
  padding: 10px;
}

input {
  margin: 10px;
}

button{
  margin: 10px;
}

.border{
  width: 60%;
}

.form-control{
  width: 50%;
}


</style>
