import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    fund: 10000,
    bmw_amount: 0,
    apple_amount: 0,
    google_amount: 0,
    twitter_amount: 0,
    bmw_price: 110,
    google_price: 100,
    apple_price: 150,
    twitter_price: 130,
    saveData: {
      savebmw: 110,
      savegoogle: 100,
      saveapple: 150,
      savetwitter: 130
    }

  },
  getters: {
    bmwItem: state => {
      return state.bmw_amount;
    },
    appleItem: state => {
      return state.apple_amount;
    },
    twitterItem: state => {
      return state.twitter_amount;
    },
    googleItem: state => {
      return state.google_amount;
    },
    bmwPrice: state => {
      return state.bmw_price;
    },
    googlePrice: state => {
      return state.google_price;
    },
    applePrice: state => {
      return state.apple_price;
    },
    twitterPrice: state => {
      return state.twitter_price;
    },
    getfund: state => {
      return state.fund;
    }
  },
  mutations: {
    updateBmwitem: (state, payload) => {
      state.bmw_amount += Number(payload);

    },
    updateGoogleitem: (state, payload) => {
      state.google_amount += Number(payload);
    },
    updateAppleitem: (state, payload) => {
      state.apple_amount += Number(payload);
    },
    updateTwitteritem: (state, payload) => {
      state.twitter_amount += Number(payload);
    },
    buyFunds: (state, payload) => {
      state.fund -= Number(payload);
    },
    sellBmwitem: (state, payload) => {
      state.bmw_amount -= Number(payload);

    },
    sellGoogleitem: (state, payload) => {
      state.google_amount -= Number(payload);
    },
    sellAppleitem: (state, payload) => {
      state.apple_amount -= Number(payload);
    },
    sellTwitteritem: (state, payload) => {
      state.twitter_amount -= Number(payload);
    },
    sellFunds: (state, payload) => {
      state.fund += Number(payload);
    },
    EndDays: state => {
      state.bmw_price = Math.round(Math.random() * 100 + 30);
      state.google_price = Math.round(Math.random() * 100 + 30);
      state.apple_price = Math.round(Math.random() * 100 + 30);
      state.twitter_price = Math.round(Math.random() * 100 + 30);
    },
    SavedBack: state => {
      state.saveData.savebmw = state.bmw_price;
      state.saveData.savegoogle = state.google_price;
      state.saveData.saveapple = state.apple_price;
      state.saveData.savetwitter = state.twitter_price;
    },
    LoadBack: state => {
      state.bmw_price = state.saveData.savebmw;
      state.google_price = state.saveData.savegoogle;
      state.apple_price = state.saveData.saveapple;
      state.twitter_price = state.saveData.savetwitter;
    }

  }

});
