<template>
<div class="container">
  <br>
  <div class="row">
    <div class="col-lg-6">
      <br>
      <div class="border">
        <h2>BMW (price: {{ bmwPrice }}) ( Quantity : {{bmwItem}})</h2>
        <input type="text" class="form-control" placeholder="Quantity" v-model="curbmw">
        <button type="button" class="btn btn-success" @click="updateBmwitem">Buy</button>
      </div>
    </div>


    <div class="col-lg-6">
      <br>
      <div class="border">
        <h2>Google (price: {{ googlePrice }}) ( Quantity : {{ googleItem }})</h2>
        <input type="text" class="form-control" placeholder="Quantity" v-model="curgoogle">
        <button type="button" class="btn btn-success" @click="updateGoogleitem">Buy</button>
      </div>
    </div>
  </div>
  <br>
  <div class="row">

    <div class="col-lg-6">
      <br>
      <div class="border">
        <h2>Apple (price: {{ applePrice }}) ( Quantity : {{ appleItem }})</h2>
        <input type="text" class="form-control" placeholder="Quantity" v-model="curapp">
        <button type="button" class="btn btn-success" @click="updateAppleitem">Buy</button>
      </div>
    </div>
    <br>

    <div class="col-lg-6">
      <br>
      <div class="border">
        <h2>Twitter (price: {{twitterPrice}}) ( Quantity : {{ twitterItem }})</h2>
        <input type="text" class="form-control" placeholder="Quantity" v-model="curtwitt">
        <button type="button" class="btn btn-success" @click="updateTwitteritem">Buy</button>
      </div>
    </div>
  </div>

</div>
</template>

<script>
import {
  mapGetters
} from 'vuex';

export default {
  data() {
    return {
      curbmw: '',
      curapp: '',
      curgoogle: '',
      curtwitt: '',

    }
  },
  computed: {
    ...mapGetters([
      'bmwItem',
      'appleItem',
      'googleItem',
      'twitterItem',
      'bmwPrice',
      'applePrice',
      'googlePrice',
      'twitterPrice',
      'getfund'
    ])

  },
  methods: {
    updateBmwitem() {
      var price = this.curbmw * this.bmwPrice;
      if (this.getfund - price > 0) {
        this.$store.commit('updateBmwitem', this.curbmw);
        this.$store.commit('buyFunds', price);
        this.curbmw = null;
      } else {
        alert('Not Enough Money');
      }
    },
    updateAppleitem() {
      var price = this.curapp * this.applePrice;
      if (this.getfund - price > 0) {
        this.$store.commit('updateAppleitem', this.curapp);
        this.$store.commit('buyFunds', price);
        this.curapp = null;
      } else {
        alert('Not Enough Money');
      }
    },
    updateGoogleitem() {
      var price = this.curgoogle * this.googlePrice;
      if (this.getfund - price > 0) {
        this.$store.commit('updateGoogleitem', this.curgoogle);
        this.$store.commit('buyFunds', price);
        this.curgoogle = null;
      } else {
        alert('Not Enough Money');
      }
    },
    updateTwitteritem() {
      var price = this.curtwitt * this.twitterPrice;
      if (this.getfund - price > 0) {
        this.$store.commit('updateTwitteritem', this.curtwitt);
        this.$store.commit('buyFunds', price);
        this.curtwitt = null;
      } else {
        alert('Not Enough Money');
      }
    },
  }
}
</script>

<style scoped>
h2 {
  background-color: #d2f5e3;
  color: darkgreen;
  border-radius: 3px;
  font-size: 15px;
  padding: 10px;
}

input {
  margin: 10px;
}

button {
  margin: 10px;
}

.border {
  width: 60%;
}

.form-control {
  width: 50%;
}
</style>
