import Vue from 'vue'
import VueRouter from 'vue-router';
import App from './App.vue'
import { routes } from './routings.js';
import { store } from './store/stored';

Vue.use(VueRouter);

const router=new VueRouter({
  routes,
});


new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})
